.UserList {
  background-color: #fff;
  height: 75px;
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  overflow-x: scroll;
  align-items: center;
}
.UserList img {
  display: block;
  min-height: 50px;
  background-color: #505050;
  border-radius: 50%;
  min-width: 50px;
  height: 50px;
  margin: 0 10px;
}

